
import { DeckofCard } from '../../../models/game/deckofCard';
import { User } from '../../../models/game/user';
import { Card } from '../../../models/game/card';
export class GameModel
{
    constructor(gname:string, gowner:User, gdeck:Card[])
    {
        this.name = gname;
        this.owner = gowner;
        this.deck = gdeck;
        this.originalDeck = gdeck;
    }
    public id: string;
    public name:string;
    public owner:User;
    public deck:Card[];
    public originalDeck:Card[];
    public users:string[] = [];
}
